import React, { useState, useEffect } from 'react';
import './App.css';
import Grid from "./js/Grid";
import ProgressBar from "./js/ProgressBar";
import checkRequirements from "./js/checkRequirements";
import {suggestion_array} from "./js/all_course_list";
import {hssList} from "./js/hss_list";

// Helper: Create a grid (array of columns) from a one-dimensional list (column-by-column)
// We now add a "placeholder" property, using the raw data.
const createGridFromColumnData = (data, gridIdx, columns, rows, editableFn, dropdownFn, suggestions, watchArr,
                                  matchCond) => {
    const grid = [];
    let index = 0;
    let suggest_idx = 0;
    for (let c = 0; c < columns; c++) {
        const col = [];
        for (let r = 0; r < rows; r++) {
            const cellData = data[index] !== undefined ? data[index] : "";
            if (editableFn(c, r)) {
                col.push({
                    content: "",
                    placeholder: cellData, // Store the original raw string as placeholder.
                    editable: true,
                    dropdown: false,
                    show: false,
                    watch: watchArr[`${gridIdx},${c},${r}`],
                    matchCond: matchCond[`${gridIdx},${c},${r}`],
                    suggested: suggestions[suggest_idx] !== undefined ? suggestions[suggest_idx]: [""],
                });
                suggest_idx++;
            } else if (dropdownFn(c, r)) {
                col.push({
                    content: cellData,
                    placeholder: "", // Store the original raw string as placeholder.
                    editable: false,
                    dropdown: true,
                    show: true,
                    watch: watchArr[`${gridIdx},${c},${r}`],
                    matchCond: matchCond[`${gridIdx},${c},${r}`],
                    suggested: suggestions[suggest_idx] !== undefined ? suggestions[suggest_idx]: [""]
                })
                suggest_idx++;
            } else {
                col.push({
                    content: cellData,
                    placeholder: cellData, // Store the original raw string as placeholder.
                    editable: false,
                    dropdown: false,
                    show: true,
                    watch: watchArr[`${gridIdx},${c},${r}`],
                    matchCond: matchCond[`${gridIdx},${c},${r}`],
                    suggested: []
                })
            }
            index++;
        }
        grid.push(col);
    }
    return grid;
};

// Raw data for each grid (list of strings provided column-by-column)
const grid1RawData = [
    "APS100H1: Orientation to Engineering",
    "APS110H1: Engineering Chemistry and Materials Science",
    "APS111H1: Engineering Strategies & Practice I",
    "CIV100H1: Mechanics",
    "MAT186H1: Calculus I",
    "MAT188H1: Linear Algebra",
    "APS106H1: Fundamentals of Computer Programming",
    "APS112H1: Engineering Strategies & Practice II",
    "ECE110H1: Electrical Fundamentals",
    "MAT187H1: Calculus II",
    "MIE100H1: Dynamics",
    "MIE191H1: Seminar Course: Introduction to Mechanical and Industrial Engineering"
];
const grid2RawData = [
    "MIE230H1: Engineering Analysis",
    "MIE231H1: Probability and Statistics with Engineering Applications",
    "MIE243H1: Mechanical Engineering Design",
    "MIE270H1: Materials Science",
    "CS/HSS Elective",
    "Other",
    "MAT234H1: Differential Equations",
    "MIE210H1: Thermodynamics",
    "MIE221H1: Manufacturing Engineering",
    "MIE222H1: Mechanics of Solids I",
    "CS/HSS Elective",
    "Other"
];
const grid3RawData = [
    "MIE258H1: Engineering Economics",
    "MIE301H1: Kinematics and Dynamics of Machines",
    "MIE312H1: Fluid Mechanics I",
    "MIE342H1: Circuits with Applications to Mechanical Engineering Systems",
    "NS Elective",
    "Other",
    "MIE313H1: Heat and Mass Transfer",
    "MIE315H1: Design for the Environment",
    "MIE334H1: Numerical Methods I",
    "Stream Course 1",
    "Stream Course 2",
    "Other"
];
const grid4RawData = [
    "MIE491Y1: Capstone Design",
    "Stream Course 1",
    "Stream Course 2",
    "CS/HSS or Technical Elective",
    "CS/HSS or Technical Elective",
    "Other",
    "MIE491Y1: Capstone Design",
    "CS/HSS or Technical Elective",
    "CS/HSS or Technical Elective",
    "CS/HSS or Technical Elective",
    "CS/HSS or Technical Elective",
    "Other",
];

const capstones = [
    "MIE491Y1: Capstone Design",
    "APS490Y1: Multi-Disciplinary Capstone Design",
    "BME498Y1: Biomedical Engineering Capstone Design"
];

// Grid dimensions: 2 columns x 6 rows.
const NUM_COLUMNS = 2;
const NUM_ROWS = 6;

const Y3Stream = [
    "MIE304H1: Introduction to Quality Control",
    "MIE346H1: Analog and Digital Electronics for Mechatronics",
    "MIE320H1: Mechanics of Solids II",
    "MIE311H1: Thermal Energy Conversion",
    "BME331H1: Physiological Control Systems",
    "CHE354H1: Cellular and Molecular Biology"
]

const Y4Stream = [
    "MIE422H1: Automated Manufacturing",
    "MIE404H1: Control Systems I",
    "MIE442H1: Machine Design",
    "MIE515H1: Alternative Energy Systems",
    "MIE439H1: Cellular and Tissue Biomechanics",
    "MIE458H1: Biofluid Mechanics",
];

const extras = new Array(12).fill("Other");

function Dashboard() {
    const [grids, setGrids] = useState([]);
    const [messages, setMessages] = useState([]);

    const streamFunc = (input, targetCell) => {
        // Define the two special sets.
        const setA = [
            "BME331H1: Physiological Control Systems",
            "CHE354H1: Cellular and Molecular Biology"
        ];
        const setB = [
            "MIE439H1: Cellular and Tissue Biomechanics",
            "MIE458H1: Biofluid Mechanics"
        ];

        // If input is in setA, then allowed target values are in setB.
        if (setA.includes(input)) {
            // If target already holds a valid value from setB, keep it.
            if (targetCell && setB.includes(targetCell.content)) {
                return targetCell.content;
            }
            // Otherwise, default to the first allowed option.
            return setB[0];
        }

        // If input is in setB, then allowed target values are in setA.
        if (setB.includes(input)) {
            if (targetCell && setA.includes(targetCell.content)) {
                return targetCell.content;
            }
            return setA[0];
        }

        // For all other courses, fall back to your original mapping.
        switch (input) {
            case "MIE304H1: Introduction to Quality Control":
                return "MIE422H1: Automated Manufacturing";
            case "MIE346H1: Analog and Digital Electronics for Mechatronics":
                return "MIE404H1: Control Systems I";
            case "MIE320H1: Mechanics of Solids II":
                return "MIE442H1: Machine Design";
            case "MIE311H1: Thermal Energy Conversion":
                return "MIE515H1: Alternative Energy Systems";
            case "MIE422H1: Automated Manufacturing":
                return "MIE304H1: Introduction to Quality Control";
            case "MIE404H1: Control Systems I":
                return "MIE346H1: Analog and Digital Electronics for Mechatronics";
            case "MIE442H1: Machine Design":
                return "MIE320H1: Mechanics of Solids II";
            case "MIE515H1: Alternative Energy Systems":
                return "MIE311H1: Thermal Energy Conversion";
            default:
                return input;
        }
    };

    useEffect(() => {
        // Initialize 4 grids using the helper function.
        // For Grid 1, cells at even positions (by column order) are fixed.
        const initialGridData = [
            createGridFromColumnData(grid1RawData, 0, NUM_COLUMNS, NUM_ROWS, () => false,
                () => false, [], {}, {}),
            createGridFromColumnData(grid2RawData, 1, NUM_COLUMNS, NUM_ROWS, (row, col) => (col === 4 || col === 5),() => false,
                [suggestion_array, suggestion_array, suggestion_array, suggestion_array], {}, {}),

            createGridFromColumnData(grid3RawData, 2, NUM_COLUMNS, NUM_ROWS,
                (row, col) => ((col === 4 || col === 5) || (col === 3 && row === 1)), () => false,
                [suggestion_array, suggestion_array, Y3Stream, Y3Stream, suggestion_array], {
                    "2,1,3": "3,0,1",
                    "2,1,4": "3,0,2"
                },
                {
                    "2,1,3": streamFunc,
                    "2,1,4": streamFunc
                }),

            createGridFromColumnData(grid4RawData, 3, NUM_COLUMNS, NUM_ROWS, (row, col) => (col !== 0),
                (col, row) => row === 0,
                [capstones, Y4Stream, Y4Stream, suggestion_array, suggestion_array, suggestion_array,
                    capstones, suggestion_array, suggestion_array, suggestion_array, suggestion_array, suggestion_array],
                {
                    "3,0,0": "3,1,0",
                    "3,1,0": "3,0,0",
                    "3,0,1": "2,1,3",
                    "3,0,2": "2,1,4"
                },
                {
                    "3,0,0": (input) => input,
                    "3,1,0": (input) => input,
                    "3,0,1": streamFunc,
                    "3,0,2": streamFunc
                }),

            createGridFromColumnData(extras, 4, NUM_COLUMNS, NUM_ROWS, () => true, () => false,
                new Array(12).fill(suggestion_array), {}, {}),
        ];
        setGrids(initialGridData);
    }, []);

    const handleCellChange = (gridIndex, colIndex, rowIndex, newData) => {
        setGrids(prevGrids => {
            // Make a copy of the grids state.
            const newGrids = prevGrids.map((grid, gIdx) => {
                // If this is not the grid we are modifying, return it unchanged.
                if (gIdx !== gridIndex) return grid;
                // Otherwise, update the grid:
                return grid.map((col, cIdx) => {
                    // If this is not the column we are modifying, return it unchanged.
                    if (cIdx !== colIndex) return col;
                    // Otherwise, update the specific row:
                    return col.map((cell, rIdx) =>
                        rIdx === rowIndex ? { ...cell, ...newData } : cell
                    );
                });
            });

            console.log(gridIndex, colIndex, rowIndex);

            // Get the source cell (the one that was just updated)
            const sourceCell = newGrids[gridIndex][colIndex][rowIndex];

            // Handle watchers!
            if (sourceCell.watch !== undefined) {

                // The watch property should be a string key like "3,1,0"
                const [targetGridStr, targetColStr, targetRowStr] = sourceCell.watch.split(',');
                const targetGridIndex = parseInt(targetGridStr, 10);
                const targetColIndex = parseInt(targetColStr, 10);
                const targetRowIndex = parseInt(targetRowStr, 10);

                // Get the matching function from the source cell (if any)
                // This function transforms the input from the source cell for the target cell.
                const matchFunction = sourceCell.matchCond;
                const targetCell = newGrids[targetGridIndex][targetColIndex][targetRowIndex];
                const newContent = matchFunction ? matchFunction(newData.content, targetCell) : newData.content;

                // Update the target cell with the new content.
                newGrids[targetGridIndex] = newGrids[targetGridIndex].map((col, cIdx) => {
                    if (cIdx === targetColIndex) {
                        return col.map((cell, rIdx) => {
                            if (rIdx === targetRowIndex) {
                                return { ...cell, content: newContent, show: true };
                            }
                            return cell;
                        });
                    }
                    return col;
                });
            }

            return newGrids;
        });
    };

    useEffect(() => {
        checkRequirements(grids, hssList, setMessages);
    }, [grids, hssList, setMessages]);

    const handleExport = () => {
        // Create CSV data from the grid
        const csvData = grids.map(grid => {
            // For each grid, map over the columns and rows and get the content or placeholder
            return grid[0].map((_, rowIndex) => {
                return grid.map((col, colIndex) => {
                    const cell = col[rowIndex];
                    return `"${cell.content || cell.placeholder}"`; // Include content or placeholder in CSV
                }).join(','); // Join columns by commas
            }).join('\n'); // Join rows by new lines
        }).join('\n\n'); // Separate different grids with new lines

        // Add the messages (if needed) at the bottom of the CSV export (or wherever necessary)
        const fullExportData = `${csvData}\n\nMessages:\n${messages.join('\n')}`;

        // Convert the CSV data into a Blob to be downloaded
        const dataStr = "data:text/csv;charset=utf-8," + encodeURIComponent(fullExportData);

        // Create a download link for the CSV file
        const downloadAnchorNode = document.createElement('a');
        downloadAnchorNode.setAttribute("href", dataStr);
        downloadAnchorNode.setAttribute("download", "dashboard_data.csv");
        document.body.appendChild(downloadAnchorNode);
        downloadAnchorNode.click();
        downloadAnchorNode.remove();
    };

    return (
        <div className="dashboard">
            <h1>Mechanical Engineering Course Planning Dashboard</h1>
            <p>Please note this is an unofficial tool that is currently still in development. Please refer to the <a href="https://engineering.calendar.utoronto.ca/">Engineering Academic Calendar</a> for
                official program requirements</p>
            <p>If you find an issue or bug, or would like to suggest a new feature, please send an email to
                matthew [dot] du [at] mail.utoronto.ca. Please check <a href="https://docs.google.com/document/d/1jSp2kRMLBUIHAByMg1pX9_lP01Y2zGK0HjqxS0RA2qQ/edit?usp=sharing">here</a> to see if the issue/feature
                has been identified</p>

            <div className="main-content">
                <div className="grids-section">
                    {grids.map((grid, idx) => (
                        <div key={idx} className="grid-wrapper">
                            {
                                idx === grids.length - 1 ? (
                                    <h2>Extra Courses</h2>
                                ) : (
                                    <h2>Year {idx + 1}</h2>
                                )
                            }
                            <Grid
                                gridId={idx}
                                data={grid}
                                onCellChange={(colIndex, rowIndex, newData) =>
                                    handleCellChange(idx, colIndex, rowIndex, newData)
                                }
                            />
                        </div>
                    ))}
                </div>

                <div className="sidebar">
                    <div className="sidebar-inner">
                        <div className="messages-container">
                            <h2>Program Requirements</h2>
                            {messages.length > 0 ? (
                                <ul>
                                    {messages.map((msg, index) => (
                                        <li key={index}>{msg}</li>
                                    ))}
                                </ul>
                            ) : (
                                <p>All requirements met!</p>
                            )}
                        </div>
                        <div className="progress-container">
                            <h2>Progress towards Minors</h2>
                            <ProgressBar grids={grids}/>
                        </div>
                        <div className="export-container">
                            <h2>Export</h2>
                            <button onClick={handleExport}>Export as CSV</button>
                        </div>
                    </div>
                </div>
            </div>

            <footer className="footer">
                Matthew Du &copy; {new Date().getFullYear()}
            </footer>
        </div>
    );
}

export default Dashboard;
